.appdownload{
    margin: auto auto;
    margin-top: 100px;
    font-size: max(3vw,20px);
    text-align: center;
    font-weight: 500;

}
.app-download-platforms{
    display: flex;
    justify-content: center;
    gap: max(2vw,10px);
    margin-top: 40px;
}
.appdownload p{
    font-family: outfit;
}
.app-download-platforms img{
    cursor: pointer;
}
.app-download-platforms img:hover{
    transform: scale(1.05);
}