.explore-menu{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.explore-menu h1{
    margin-top: 10px;
    color: #262626;
    font-weight: 500;
    font-family: outfit;
}
.explore-menu-text{
    max-width: 60%;
    columns: #808080;
    
}
.menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px;
    overflow-x: scroll;
}
.menu-list::-webkit-scrollbar{
    display: none;
}
.explore-menu-list-item img{
    width: 7.5vw;
    min-width: 80px;
    cursor: pointer;
    border-radius: 50%;
    transition: 0.2s;
}
.explore-menu-list-item p{
    margin: 10px;
    color: #747474;
    font-size: max(1.4vw,16px);
    cursor: pointer;


}
.explore-menu-list-item .active{
    border: 4px solid tomato;
    padding: 2px;
}

