.FoodDisplay{
    margin-top: 30px;
}
.FoodDisplay h2{
    font-size: max(2vw,24px);
    font-weight: 600;
    font-family: outfit;
}
.food-display-list{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(240px,1fr));
    margin-top: 30px;
    gap: 40px;
    row-gap: 50px;
}