.header{
    height: 34vw;
    margin: 30px auto;
    background: url('header_img.png') no-repeat;
    background-size: contain;
    position: relative;
}
.headercontents{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1.5vw;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s;

}
.headercontents h2{
    font-weight: 500;
    color: white;
    font-size: max(4.5vw,22px);
}
.headercontents p{
    color: white;
    font-size: 1.5vw;
}
.headercontents button{
    border: none;
    color: #747474;
    font-weight: 500;
    padding: 1vw 2.3vw;
    background-color: white;
    font-size: max(1vw,13px);
    border-radius: 50px;

}